<template>
  <div class="headerSize">
    <div id="header">
      <img @click="toHome" class="logoKHome" src="../assets/K.svg" />
      <img @click="openMenu" src="../assets/MenuSandwich.svg" />
    </div>
    <Menu :isShowing="isShowingMenu" v-on:close="onMenuClose($event)" />
  </div>
</template>

<script>
import Menu from '@/components/Menu.vue'

export default {
  name: 'Header',
  components: { Menu },
  methods: {
    toHome() {
      this.$router.push({ name: 'Home' }).catch(() => {})
      this.closeMenu()
    },
    openMenu() {
      this.isShowingMenu = true
    },
    closeMenu() {
      this.isShowingMenu = false
    },
    onMenuClose(toHome) {
      toHome ? this.toHome() : this.closeMenu()
    }
  },
  data() {
    return {
      isShowingMenu: false
    }
  }
}
</script>

<style scoped>
.logoKHome {
  filter: invert(11%) sepia(36%) saturate(963%) hue-rotate(157deg)
    brightness(102%) contrast(104%);
}

.headerSize {
  width: 100%;
}

#header {
  align-self: flex-start;
  display: flex;
  padding: 2% 3% 2% 3%;
  justify-content: space-between;
  align-items: center;
}
</style>
